/**** Effects ****/
.animation a {
  position: relative;
  color: white;
}
.animation a:hover {
  color: white;
}
.animation a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.05rem;
  bottom: 0;
  left: 0;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}
.animation a:hover:before {
  visibility: visible;
  background-color: #fff;
  transform: scaleX(1);
}

