}

.scrollTop {
  position: fixed; 
  width: 3rem;
  bottom: 20px;
  height: 20px;
  z-index: 1000;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.5;
  color: gray;
  right: 1rem;
  
}

.scrollTop:hover{
  opacity: 10;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1.0;
  }
}



